.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #000;
  color: #a0541b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
}

.App-tagline {
  opacity: 0.7;
}

.App-header img {
  width: 100px;
  height: auto;
}

.App-link {
  color: #61dafb;
  opacity: 0.5;
}
